<p>该方法实现了水印的生成，但仍存在几个问题</p>
<ul><li>调整页面分辨率后，水印没有进行动态调整适配</li>
<li>可通过手动去除页面元素方式进行删除</li></ul>
<p>故做了一些调整</p>
<ol><li>对所有水印元素添加了监控，一旦发现有变化立即重新生成</li>
